
.app {
  text-align: center;
  background-color: #282c34;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.appHeader {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #EFEAE6;
  padding: 20px 0;

}

.locationContainer {
  margin: 10px 0;
}

.iconosContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top:30px
}

.icono {
  color: #EFEAE6;
  font-size: calc(20px + 6vmin);
  margin-right: 20px;

}

.musicIcon {
  width: 5vmin;
}

.buskingCity{
  margin-top: 0;
}

.currentlyParagraph {
  margin-bottom: 0;
  font-size: calc(7px + 2vmin);
}

.reproductor {
  max-width: 90%;
  min-width: 300px;
  width: 600px;
  height: 300px;
}